.header {
  min-height: 120px;
  max-width: 880px;
  width: 100%;
  margin: 0 auto;
  padding: 45px 0 40px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  box-sizing: border-box;
  display: flex;
  color: #fff;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 990px) {
  .header {
    min-height: 84px;
    padding: 28px 27px 31px;
  }
}
